/* src/components/Card.css */

.project-list {
    display: flex;
    flex-wrap: wrap;
}

.card {
    flex: 1 0 45%; /* Each card spans half of the row */
    margin: 10px; /* Adjust margin as needed */
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.card h2 {
    margin-top: 0;
}

.card p {
    margin: 10px 0;
}

.card a {
    display: block;
    text-align: center;
    padding: 10px;
    background-color: #007bff;
    color: #ffffff;
    text-decoration: none;
    border-radius: 4px;
}

.card a:hover {
    background-color: #0056b3;
}
