.home-container {
    padding: 40px;
    text-align: center;
}

.home-title {
    font-size: 36px;
    color: #333;
    margin-bottom: 20px;
}

.home-description {
    font-size: 18px;
    color: #666;
    margin-bottom: 30px;
}

.home-button {
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.home-button:hover {
    background-color: #0056b3;
}

.home-section {
    margin-top: 50px;
}

.home-section-title {
    font-size: 24px;
    color: #444;
    margin-bottom: 20px;
}

.social-links {
    margin-top: 20px;
}

.social-links a {
    margin-right: 10px;
    color: #007bff;
    font-size: 24px;
    transition: color 0.3s ease;
}

.social-links a:hover {
    color: #0056b3;
}
