.hobbies-container {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.hobbies-heading {
    font-size: 32px;
    color: #333;
    margin-bottom: 10px;
    text-align: center;
}

.hobbies-description {
    font-size: 16px;
    color: #555;
    margin-bottom: 20px;
    text-align: center;
}

.hobbies-section {
    margin-top: 40px;
}

.hobbies-subheading {
    font-size: 24px;
    color: #444;
    margin-bottom: 15px;
}

.anime-list {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
}

.anime-item {
    font-size: 18px;
    color: #666;
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
}

.anime-item:hover {
    background-color: #e0e0e0;
}
